.premiumButtonContaier{
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    position: fixed;
    right:0;
    top:90px;
    color:'teal';
    z-index: 999;
    background-color: white;
    padding:14px 20px;
    border-top-left-radius: 40px;
    border-bottom-left-radius: 40px;
    border-top: 1px solid teal;
    border-bottom: 1px solid teal;
    border-left: 1px solid teal;
    box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.5);
    transition: 0.7s;
    
}
.premiumButtonContaier:hover{
    background-color: lightcyan;
}
.animation-entering{
    animation: animate 0.8s infinite ease-in-out alternate;
}

@keyframes animate{
    0% {
        transform: scale(1);
    }
    100% {
        transform: scale(1.1);
    }
}


