.orderPlace{
    width:'100%';
    min-height: 100vh;
    /* background-color: blue; */
    padding:5%;
    padding-top:7%;
    display:flex;
    
}
.orderPlace .left{
    width:70%;
    /* background-color: red; */

}
.orderPlace .right{
    width:30%;
    display: flex;
    justify-content: flex-end;
    /* background-color: green; */
}


/* left */
.gigSection{
    display: flex;
    justify-content: space-between;
    padding-bottom:10px;
    border-bottom: 1px solid lightgrey;
}
.gigSection .left{
    width:60%;
    /* background-color: cyan; */
    display: flex;
}
.gigSection .right{
    width:10%;
    text-align: end;
    font-weight: bold;
    font-family: sans-serif;
    color:grey
}
.gigSection .gigImage{
    width:200px;
    height:100px;
    margin-right: 10px;
}
.gigSection .gigTitle{
    color:black;
    font-weight: bold;
    font-size: 18px;
    font-family: sans-serif;
}
.gigSection .stars-div{
    padding:0;
    margin:0;
    margin-right:10px;
}


.orderPlace .left .requirementsTextAreaContainer{
    padding-top:28px;
    /* box-shadow: 0px 0px 40px rgba(0,0,0,0.4); */
    /* background-color: blue; */
    width:100%;
 }
 .orderPlace .left .premiumTextAreaTop{
     background-color: rgb(39, 38, 38);
     width:100%;
     /* padding:16px; */
     font-weight: bold;
     font-family: sans-serif;
     text-transform: uppercase;
     color:white;
     
 }
 .orderPlace .left .premiumTextArea{
     width:100%;
     /* margin-left: calc(10% - 10px); */
     /* height:200px; */
     vertical-align: top;
     resize: none;
     /* padding:16px; */
     font-size: 16px;
     font-family: sans-serif;
     border:none;
     border:2px solid rgb(39, 38, 38);
     outline: none;
 }







/* right */
.checkoutContainer{
    width:70%;
    height: 320px;
    position: sticky;
    top:6%;
    border:1px solid lightgray;
    padding:16px    
}
.checkoutContainer .title{
    color:black;
    font-size: 20px;
    font-family: sans-serif;
}   
.checkoutContainer .mid{
    padding:20px 0;
    border-bottom: 1px solid lightgray;
}
.checkoutContainer .mid .child{
    font-size: 16px;
    font-weight: bold;
    color:grey;

}
.checkoutContainer .bottom{
    padding:16px 0;
}
.checkoutContainer .bottom .child{
    font-size: 16px;
    font-weight: bold;
    color:grey;
}
.checkoutContainer .bottom .child1{
    font-size: 17px;
    font-weight: bold;
    color:rgb(88, 88, 88);
}

.checkoutContainer .orderPlaceButton{
    text-decoration: none;
    /* width:100%; */
    color:blue;
    margin:0 auto;
    background-color: teal;
    color:white;
    padding:10px 20px;
    border-radius: 4px;
    cursor: pointer;

}