.loading2 {
    width: 100vw;
    height: 20vh;
    background-color: white;
    display: grid;
    place-items: center;
    max-width: 100%;
  }
  
  .loading2 > div {
    width: 4vmax;
    height: 4vmax;
    border-bottom: 5px solid rgba(0, 0, 0, 0.719);
    border-left: 5px solid rgba(0, 0, 0, 0.719);
    border-right: 5px solid rgba(0, 0, 0, 0.719);
    border-top: 5px solid white;
  
    border-radius: 50%;
  
    animation: loadingRotate 800ms linear infinite;
  }
  
  @keyframes loadingRotate {
    to {
      transform: rotateZ(-360deg);
    }
  }



