.dropdown-menu {
  /* background: red; */
  width:auto;
  position: absolute;
  top: 80px;
  list-style: none;
  text-align: start;
  z-index: 100;
  transition: all 1s ease;
  /* border-radius: 4px; */
  /* background-color: rgb(250, 250, 250); */

}


.dropdown-menu li {
  /* background: rgb(0, 109, 109); */
  background-color: white;
  cursor: pointer;
  font-family: sans-serif;
  color:black;
  padding:4px 16px;
  /* border-radius:10px; */
  border:1px solid lightgrey;



}

.dropdown-menu li:hover {
  /* background-color:black; */
  transition: all 1s ease;
  color:white;

}

.dropdown-menu.clicked {
  display: none;
}

.dropdown-link {
  display: block;
  height: 100%;
  width: 100%;
  text-decoration: none;
  color:black;
  font-weight: 300;
  padding:8px 16px;
  transition: all 0.2s ease-in-out;
  font-weight: 300;

}
.dropdown-link:hover{
  color:rgb(0, 109, 109);
}

@media screen and (max-width: 960px) {
  .fa-caret-down {
    display: none;
  }
}
