.detail-container{
    width:100%;
    /* background-color:blue; */
    display:flex;
    justify-content:center;
}
.detail-inner{
    max-width:1400px;
    width:90%;
    position:relative;
    padding-top: 4%;
    /* background-color:red; */
}
.detail-inner .top{
    padding:20px 0;

    border-bottom: 1px solid lightgrey;
    text-align: start;
}
.top .edit-link{
    text-decoration: none;
    color:teal;
    font-size: 22px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    cursor: pointer;
}
.header-section{
    margin-top: 40px;
    display: flex;
    justify-content: space-between;
}
.header-section .left-container{
    width: 60%;
}
.left-container .title{
    font-size:30px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;

}
.left-container .little-info{
    padding: 30px 20px 20px 0;
    display: flex;
    align-items: center;
    /* background-color:blue; */
    border-bottom: 1px solid lightgrey;
}

.little-info span {
    margin:0 10px;
    font-size:18px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;

}
.little-info .stars-div{
    margin:0 10px;

}
.little-info .rating-container{
    color: orange;
    font-size:18px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}


.left-container .image-container{
    width:100%;
    height:500px;
    border:none;
    border:1px solid lightgrey;
    margin-top: 20px;

}
.left-container .image-container > img{
    width:100%;
    height:500px;
    object-fit:cover;
}

.left-container .about-container{
    /* background-color:red; */
}


.left-container .about-container{
    margin:40px 0;
}
.left-container .about-container .about-top{
    font-size: 28px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    margin-bottom:10px;
}
.left-container .about-container .about-desc{
    font-size: 20px;
    font-family: sans-serif;
}


.sellerInfo-container .sellerInfo-top{
    font-size: 28px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    margin-bottom:10px;
}

.sellerInfo-container .sellerInfo-design{
    display: flex;
    /* background-color:red; */
    align-items: center;

}

.sellerInfo-container .sellerInfo-image{
    width: 130px;
    height: 130px;
    margin-right: 20px;
}
.sellerInfo-container .sellerInfo-name{
    font-size:24px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.sellerInfo-container .sellerInfo-button{
    margin-top: 10px;
    padding:10px 20px;
    border:1px solid lightgrey;
    border-radius: 4px;
    color:teal;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}



.left-container  .description-container{
    border:1px solid lightgrey;
    margin-top: 20px;
    
}

.description-container-top{
    display: flex;
    justify-content:center;
    border-bottom:1px solid lightgrey;
    padding:20px 0 ;

    
}
.description-container-top > div{
    width: 50%;
    text-align: center;
    font-size: 20px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.description-container-bottom{
    padding:20px ;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    
}


.left-container .tags-container{
    margin-top: 20px;
}
.left-container .tags-container div{
    margin-bottom: 10px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.tags-container span{
    background-color:lightgrey;
    margin-right:10px;
    padding:4px 10px;
    border-radius: 4px;
}





.header-section .right-container{
    width: 28%;
    height:400px;
    position:sticky;
    top:40px;
    border: 1px solid teal;
    position:'relative';
    /* border-radius: 14px; */
    transition: all 0.3s ease-in;
    /* box-shadow: 0px 0px 40px 2px rgba(0,0,0,0.2 ); */
    /* border-bottom: 10px solid lightgrey; */
}


.right-container .right-container-top{
    display: flex;
    /* padding:20px 0;
    justify-content: space-evenly;
    background-color:blue; */
}
.right-container .right-container-top > .inner{
    font-size:18px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    padding:20px 0;
    flex:1;
    text-align:center;
    cursor:pointer;
    text-transform: uppercase;
    border-bottom: 1px solid lightgrey;

    /* color:black; */
}
.right-container-top-active{
    color: white;
    background-color: teal;

}


.right-container .middle{
    padding:20px ;

}
.right-container .middle .amount{
    color:black;
    font-size:26px;
    margin-bottom: 20px;
    font-family:  sans-serif;
    font-weight: bold;
}
.right-container .middle .name{
    font-family: sans-serif;
    font-size: 20px;
    margin-right: 10px;
    font-weight: bold;
}
.right-container .middle .desc{
    font-size: 20px;
    font-family: sans-serif;

}
.right-container .bottom-container{
    position:absolute;
    bottom:10px;
    width: 100%;
    display: flex;
    justify-content:center;
    flex-direction:column;
    align-items:center;
    text-decoration: none;
}
.right-container .bottom-container .delivery{
    align-self: start;
    /* background-color:red; */
    margin-left: 6px;
    font-size: 22px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    align-items: center;
    /* margin-bottom: 10px; */
}
.right-container .bottom-container .button{
    width:90%;
    /* background-color:teal; */
    color:white;
    padding:10px 0;
    font-size: 20px;
    text-align:center;
    border-radius:4px;
    text-decoration: none;
    /* cursor: pointer; */
}